// 注册模块
// 引入Vue
import Vue from 'vue'
import Router from 'vue-router'

//六合彩
import _6hecai from "@/components/game/6hecai/6hecai";



Vue.use(Router)

export default new Router({
    routes: [
        {
            path: "/",
            name: "6hecai",
            meta: {
                index: 1,
                keepAlive: true
            },
            component: _6hecai
        }
    ]
})



